import React, { useEffect, useState } from "react";
import { Routes, Route, useNavigate } from "react-router-dom";
import "./App.css";
import Home from "./Home";
import Scommesse from "./Scommesse";
import FighterProfile from "./FighterProfile";

function StartScreen() {
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    // Suono all'avvio
    const punchSound = new Audio("/punch.mp3");
    punchSound.volume = 0.8;
    punchSound
      .play()
      .catch(() => console.warn("Autoplay bloccato — serve interazione utente"));

    // Navigazione automatica dopo 3 secondi
    const timer = setTimeout(() => {
      setLoading(false);
      navigate("/home");
    }, 3000);

    return () => clearTimeout(timer);
  }, [navigate]);

  return (
    <div className="start-screen">
      {loading && <img src="/logo.png" alt="KOZone Logo" className="start-logo" />}
    </div>
  );
}

function App() {
  return (
    <Routes>
      <Route path="/" element={<StartScreen />} />
      <Route path="/home" element={<Home />} />
      <Route path="/fighter/:name" element={<FighterProfile />} /> 
      <Route path="/scommesse" element={<Scommesse />} />
    </Routes>
  );
}

export default App;
