import React, { useState } from "react";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";
import { db } from "./firebase";
import { ref, push } from "firebase/database";
import Navbar from "./Navbar";
import "./Home.css";

const matches = [
  { date: "08/11", time: "??:00", fighter1: "Laurino C.", fighter2: "Martoccia A.", odd1: 4.80, odd2: 1.13 },
  { date: "08/11", time: "??:00", fighter1: "Lomio D.", fighter2: "Guidone G.", odd1: 1.05, odd2: 7.20 },
  { date: "08/11", time: "??:00", fighter1: "Oddone M.", fighter2: "Mussuto A.", odd1: 1.05, odd2: 7.20 },
  { date: "08/11", time: "??:00", fighter1: "Verrastro M.", fighter2: "Madonna Th.", odd1: 1.51, odd2: 2.33 },
  { date: "08/11", time: "??:00", fighter1: "Quadratino G.", fighter2: "Angarola D.", odd1: 1.11, odd2: 5.20 },
  { date: "08/11", time: "??:00", fighter1: "Pecoriello P.", fighter2: "Vizza M.", odd1: 1.11, odd2: 5.20 },
];

function Home() {
  const [betslip, setBetslip] = useState([]);
  const [name, setName] = useState("");
  const importo = 1.0;

  // Seleziona una quota per incontro
  const toggleBet = (match, selection) => {
    const existingIndex = betslip.findIndex(
      (b) => b.match.fighter1 === match.fighter1 && b.match.fighter2 === match.fighter2
    );

    // Se l'incontro è già nella schedina
    if (existingIndex !== -1) {
      const updated = [...betslip];

      // Se clicchi di nuovo sulla stessa selezione, la rimuovi
      if (updated[existingIndex].selection === selection) {
        updated.splice(existingIndex, 1);
      } else {
        // Altrimenti cambia la selezione (1 <-> 2)
        updated[existingIndex] = { ...updated[existingIndex], selection };
      }

      setBetslip(updated);
    } else {
      // Se è un nuovo incontro, lo aggiunge
      setBetslip([...betslip, { match, selection }]);
    }
  };

  const removeBet = (index) => {
    const updated = [...betslip];
    updated.splice(index, 1);
    setBetslip(updated);
  };

  // Calcolo della quota totale
  const totalOdds =
    betslip.length > 0
      ? betslip
          .map((b) => (b.selection === "1" ? b.match.odd1 : b.match.odd2))
          .reduce((acc, curr) => acc * curr, 1)
          .toFixed(2)
      : 0;

  // Calcolo bonus: aumenta col numero di partite
  const getBonusPercent = (numMatches) => {
    if (numMatches < 3) return 5;
    if (numMatches < 5) return 10;
    if (numMatches < 7) return 15;
    return 20; // massimo bonus 15%
  };

  const bonusPercent = getBonusPercent(betslip.length);
  const bonusAmount = ((totalOdds * bonusPercent) / 100).toFixed(2);
  const totalWithBonus = (parseFloat(totalOdds) + parseFloat(bonusAmount)).toFixed(2);
  const potentialWin = (importo * totalWithBonus).toFixed(2);

  // Invio a Firebase
  const handleSubmit = async () => {
    if (!name.trim()) {
      alert("Inserisci il tuo nome prima di piazzare la scommessa!");
      return;
    }

    if (betslip.length === 0) {
      alert("Schedina vuota!");
      return;
    }

    try {
      await push(ref(db, "schedine"), {
        name,
        betslip,
        totalOdds,
        bonusPercent,
        potentialWin,
        timestamp: new Date().toISOString(),
      });

      alert("Schedina inviata con successo 💸");
      setBetslip([]);
      setName("");
    } catch (error) {
      console.error("Errore nel salvataggio:", error);
      alert("Errore nel salvataggio della schedina ❌");
    }
  };

  return (
    <div className="home-container">
      <Navbar />

      <motion.div
        className="home-content"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.6 }}
      >
        {/* ====== INCONTRI ====== */}
        <div className="matches-section">
          <h2 className="section-title">
            🥊 Boxe <span>Mondiale</span>
          </h2>

          <table className="matches-table">
            <thead>
              <tr>
                <th>Data</th>
                <th>Ora</th>
                <th>Incontro</th>
                <th>1</th>
                <th>2</th>
              </tr>
            </thead>
            <tbody>
              {matches.map((m, i) => {
                const existingBet = betslip.find(
                  (b) => b.match.fighter1 === m.fighter1 && b.match.fighter2 === m.fighter2
                );
                return (
                  <tr key={i}>
                    <td>{m.date}</td>
                    <td>{m.time}</td>
                    <td>
                      <div className="fighters">
                        <Link to={`/fighter/${encodeURIComponent(m.fighter1)}`} className="fighter-link">
                          {m.fighter1}
                        </Link>
                        <Link to={`/fighter/${encodeURIComponent(m.fighter2)}`} className="fighter-link">
                          {m.fighter2}
                        </Link>
                      </div>
                    </td>
                    <td>
                      <button
                        className={`odd-btn ${
                          existingBet?.selection === "1" ? "active" : ""
                        }`}
                        onClick={() => toggleBet(m, "1")}
                      >
                        {m.odd1}
                      </button>
                    </td>
                    <td>
                      <button
                        className={`odd-btn ${
                          existingBet?.selection === "2" ? "active" : ""
                        }`}
                        onClick={() => toggleBet(m, "2")}
                      >
                        {m.odd2}
                      </button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>

        {/* ====== SCHEDINA ====== */}
        <div className="betslip-box">
          <h3 className="betslip-title">🧾 Schedina</h3>

          {betslip.length === 0 ? (
            <div className="betslip-empty">
              <p>Schedina vuota 😴</p>
              <p className="hint">Aggiungi una scommessa per iniziare</p>
            </div>
          ) : (
            <>
              <div className="betslip-list">
                {betslip.map((b, i) => (
                  <div key={i} className="betslip-item">
                    <div className="betslip-info">
                      {b.match.fighter1} vs {b.match.fighter2}
                    </div>
                    <div className="betslip-actions">
                      <div
                        className="trash"
                        onClick={() => removeBet(i)}
                        style={{ cursor: "pointer" }}
                      >
                        🗑️
                      </div>
                      <div className="odd">
                        {b.selection === "1" ? b.match.odd1 : b.match.odd2}
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              <div className="betslip-amount">
                <span>Importo:</span>
                <input type="text" value="1.00 €" readOnly />
              </div>

              <div className="betslip-total">
                Quota Totale: <span>{totalOdds}</span> <br />
                Bonus: <span>+{bonusPercent}%</span> <br />
                Vincita Potenziale: <strong>{potentialWin} €</strong>
              </div>

              <input
                type="text"
                placeholder="Inserisci il tuo nome"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="name-input"
              />

              <button className="place-bet" onClick={handleSubmit}>
                Piazza Scommessa ({betslip.length})
              </button>
            </>
          )}
        </div>
      </motion.div>
    </div>
  );
}

export default Home;
