import React from "react";
import { Link, useLocation } from "react-router-dom";
import "./Navbar.css";
import logo from "./img/logo.png";

const Navbar = () => {
  const location = useLocation();

  const linksLeft = [
    { name: "Home", path: "/" },
    { name: "Match", path: "/match" },
  ];

  const linksRight = [
    { name: "Scommesse", path: "/scommesse" },
    { name: "Contatti", path: "/contact" },
  ];

  const isActive = (path) => location.pathname === path;

  return (
    <nav className="navbar">
      <div className="navbar-inner">
        <ul className="navbar-links navbar-left">
          {linksLeft.map((link) => (
            <li key={link.path}>
              <Link to={link.path} className={isActive(link.path) ? "active" : ""}>
                {link.name}
              </Link>
            </li>
          ))}
        </ul>

        <div className="navbar-logo">
          <Link to="/">
            <img src={logo} alt="KOZone Logo" draggable="false" />
          </Link>
        </div>

        <ul className="navbar-links navbar-right">
          {linksRight.map((link) => (
            <li key={link.path}>
              <Link to={link.path} className={isActive(link.path) ? "active" : ""}>
                {link.name}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
