import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import { ref, get, child } from "firebase/database";
import Navbar from "./Navbar";
import "./Scommesse.css";

function Scommesse() {
  const [schedine, setSchedine] = useState([]);
  const [selected, setSelected] = useState(null);

    useEffect(() => {
    const fetchSchedine = async () => {
        const dbRef = ref(db);
        const snapshot = await get(child(dbRef, "schedine"));
        if (snapshot.exists()) {
        const data = snapshot.val();
        const lista = Object.entries(data).map(([id, value]) => ({
            id,
            ...value,
        }));
        setSchedine(lista.reverse()); // ordina per ultime aggiunte
        } else {
        setSchedine([]);
        }
    };
    fetchSchedine();
    }, []);
    useEffect(() => {
        if (selected) {
            document.body.classList.add("modal-open");
        } else {
            document.body.classList.remove("modal-open");
        }
    }, [selected]);


  const closeModal = () => setSelected(null);


  return (
    <div className="scommesse-page">
        <div className="Navbar-scommesse">
            <Navbar />
        </div>
      
      <div className="scommesse-content">
        <h2 className="page-title">💸 Tutte le schedine piazzate</h2>

        {schedine.length === 0 ? (
          <p className="no-bets">Nessuna schedina trovata 😴</p>
        ) : (
          <div className="schedine-grid">
            {schedine.map((s) => (
              <div
                className="schedina-card"
                key={s.id}
                onClick={() => setSelected(s)}
              >
                <h3 className="schedina-title">👤 {s.name}</h3>
                <ul className="schedina-list">
                  {s.betslip.map((b, i) => (
                    <li key={i}>
                      {b.match.fighter1} vs {b.match.fighter2} →{" "}
                      <strong>{b.selection}</strong>
                    </li>
                  ))}
                </ul>
                <p>
                  <strong>Quota Totale:</strong> {s.totalOdds}
                </p>
                <p>
                  <strong>Vincita Potenziale:</strong> {s.potentialWin} €
                </p>
                <p className="timestamp">
                  📅 {new Date(s.timestamp).toLocaleString()}
                </p>
              </div>
            ))}
          </div>
        )}
      </div>

      {selected && (
        <>
          <div className="overlay" onClick={closeModal}></div>
          <div className="schedina-modal">
            <h3>🧾 Schedina di {selected.name}</h3>

            <div className="modal-list">
              {selected.betslip.map((b, i) => (
                <div key={i} className="modal-item">
                  <span>{b.match.fighter1} vs {b.match.fighter2}</span>
                  <span className="quota">
                    {b.selection === "1" ? b.match.odd1 : b.match.odd2}
                  </span>
                </div>
              ))}
            </div>

            <div className="modal-info">
              <p><strong>Importo:</strong> 1.00 €</p>
              <p><strong>Vincita Potenziale:</strong> {selected.potentialWin} €</p>
            </div>

            <button className="close-btn" onClick={closeModal}>Chiudi</button>
          </div>
        </>
      )}
    </div>
  );
}

export default Scommesse;
